<template>
  <div class="login-container">
    <h1>Вход</h1>
    <form class="login-form">
      <input type="email" placeholder="Электронная почта" v-model="email">
      <input type="password" placeholder="Пароль" v-model="password">
      <input type="submit" class="btn btn-primary" value="Войти" @click="login($event)">
    </form>
    
  </div>
</template>

<script>
// import eventBus from "./eventBus";

export default {
  name: 'Login',
  data(){
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    login(event){
      console.log(event)
      // event.preventDefault()
      var data = {
        email: this.email,
        password: this.password
      }
      
      this.request.login(data).then(tokens=>{
        console.log(tokens)
        // save tokens.accessToken
        localStorage.accessToken = tokens.accessToken
        // save tokens.refreshToken
        localStorage.refreshToken = tokens.refreshToken
        //redirect на login
        this.$eventBus.$emit('reloadApp')
        this.$router.push('/')
      }) 
      
    }
  }
}
</script>

