import {sendRequest} from './facade'

function createGiving(data){
    return sendRequest({
        method: 'POST',
        url: '/api/givings',
        data: data,
        token: localStorage.accessToken
    })
}

function deleteGiving(givingId){
    return sendRequest({
        method: 'DELETE',
        url: `/api/givings?givingId=${givingId}`,
        token: localStorage.accessToken
    })
}

export {
    createGiving,
    deleteGiving
}