<template>
  <div class="card-giving-information">
    <!-- <form v-show="!isLoad"> -->
    <!-- <h2>{{folderName}}</h2> -->
    
    <!-- <p>Кто дал: <input type="text" v-model="createdGiving.whoGave"></p> -->
      <p>Кому дал: <input type="text"  v-model="createdGiving.whoTakeName" ></p>
      <p>Телефон получателя: <input type="text" v-model="createdGiving.whoTakePhone"></p>
      <p>Дата выдачи: <input type="text" v-model="createdGiving.givingDate" placeholder="YYYY-MM-DD"></p>
      <p>Ожидаемая дата возврата: <input type="text" v-model="createdGiving.returnDate" placeholder="YYYY-MM-DD"></p>
      <p>Залог: <input type="text" v-model="createdGiving.pledge" placeholder="Цифры"></p>
      <button class="circleButton" @click="loading($event)">Отдать</button>
   <!-- </form> -->
  <div  v-if="isLoad">
    <H1>Сохранено</H1>
    <p>можете заверишить редактирование, нажав на кнопку Done</p>
  </div>
    
  </div>
</template>

<script>
import {getUser} from '../requests/users'
import {createGiving} from '../requests/givings'

export default {
  name: 'CreateGiving',
  props: {
    item: Object,
    folderName: String
  },
  data: function(){
    return {
      isLoad: false,
      createdGiving:{
        whoGave: '',
        whoTakeName: '',
        whoTakePhone: '',
        givingDate : '',
        returnDate: '',
        pledge: ''
      },
      user: {}
    }
  },
  methods:{
    loading(event){
      if(event){
        event.preventDefault();
      }
      this.isLoad = true
      var data = {
        itemId: this.item.id,
        date: this.createdGiving.givingDate,
        returnDate: this.createdGiving.returnDate,
        userId: this.user.id,
        whoTakeName: this.createdGiving.whoTakeName,
        whoTakePhone: this.createdGiving.whoTakePhone,
        pledge: this.createdGiving.pledge 
      }
      console.log(data)
      createGiving(data).then(response=>{
        console.log(response)
        this.$emit('givingCreated')
      })
    }
  },
  mounted(){
    getUser().then(user=>{
        console.log(user)
        
        this.user = user
      }, error=>{
        //Нужно сделать обновление пары токенов.
        console.log('error handler ', error)
        this.request.refreshToken().then(response=>{
          console.log(response)
        })
      })
  },
  computed: {
    from(){
      return this.folderName
    }
  }
}
</script>

