<template>
  <div class="login-container">
    <h1>Регистрация</h1>
    <form class="login-form">
      <input type="text" placeholder="Имя" v-model="name">
      <input type="text" placeholder="Электронная почта" v-model="login">
      <input type="password" placeholder="Пароль" v-model="password">
      <input type="password" placeholder="Повторите пароль" v-model="repassword">
      <input type="submit" class="btn btn-primary" value="Зарегистрироваться" @click="register()">
      <!-- <button class="btn btn-primary" @click="register()">Зарегистрироваться</button> -->
    </form>
  </div>
</template>

<script>
export default {
  name: 'Registration',
  data(){
    return {
      name: '',
      login: '',
      password: '',
      repassword: ''
    }
  },
  methods: {
    register(){
      var data = {
        user: {
          name: this.name,
          login: this.login,
          password: this.password
        }
      }
      if(this.password == this.repassword){
        this.request.register(data).then(tokens=>{
          console.log(tokens)
          // save tokens.accessToken
          localStorage.accessToken = tokens.accessToken
          // save tokens.refreshToken
          localStorage.refreshToken = tokens.refreshToken
          //redirect на login
          this.$router.push('login')
        }) 
      }
    }
  }
}
</script>

